.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navi-bar {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.navi-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navi-item {
  margin: 0;
  padding-left: 20px;
}

.navi-item:first-child {
  padding: 0;
}

.navi-link {
  color: #060b35;
  text-align: left;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.navi-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  text-align: left;
  text-transform: uppercase;
  margin-left: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card-container {
  color: #060b35;
  grid-template-columns: repeat(1, 200px);
  place-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .card-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .card-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .card-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  margin-bottom: 40px;
  align-self: center;
  width: 160px;
  height: 134px;
  margin-inline: 3px;
}

.product-card__title {
  text-align: left;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  margin-bottom: 24px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: .5px;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--inactive {
  background-image: url("star.822d561e.svg");
}

.reviews {
  margin-left: auto;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.price {
  justify-content: space-between;
  height: 18px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__number {
  font-size: 16px;
  font-weight: 700;
}

.button-buy {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.page {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

.body {
  box-sizing: border-box;
  place-content: center;
  width: 100%;
  margin: 0;
}
